.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.paragraph {
  color: #4d4d4d;
  padding: 5rem 0 3rem;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  text-align: center;
}

@media (max-width: 768px) {
  .paragraph {
    padding: 2rem 0 1rem;
  }
}
