.container {
  width: 47%;
}

.articleReview {
  width: 90%;
  margin: 0 auto;
}
.title {
  font-family: "Rufina", serif;
  color: #000000;
  font-size: 3rem;
  line-height: 4rem;
  font-weight: 700;
  padding: 1.5rem 0;
  border-bottom: 4px dashed #000000;
}

.description {
  color: #4d4d4d;
  padding: 2rem 0;
}

.generalInfo {
  color: #5e6600;
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 1.375rem;
  font-family: "Lato", sans-serif;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding-top: 1.5rem;
}

.descriptionItem {
  padding-right: 1rem;
  position: relative;
  padding-left: 1rem;
}

.descriptionItem:nth-child(2) {
  padding-left: 0;
}

.descriptionItem::after {
  content: " ";
  position: absolute;
  background: #5e6600;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  display: inline-block;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto 0;
}

.descriptionItem:last-child:after {
  display: none;
}

@media (max-width: 1400px) {
  .title {
    font-size: 2.4rem;
    line-height: 3.5rem;
  }
}

@media (max-width: 1200px) {
  .title {
    font-size: 2rem;
    line-height: 3rem;
  }
}

@media (max-width: 992px) {
  .title {
    font-size: 1.75rem;
    line-height: 2rem;
    border-bottom: 3px dashed #000000;
  }

  .generalInfo {
    font-size: 1rem;
    line-height: 1.375rem;
    position: relative;
    padding-left: 6rem;
    justify-content: flex-end;
  }

  .descriptionItem {
    width: 70%;
    padding: 0;
  }

  .avatar {
    position: absolute;
    top: 0;
    left: 0;
    width: 6rem;
  }
}

@media (max-width: 768px) {
  .container {
    width: 100%;
    margin-bottom: 2rem;
  }

  .title {
    font-size: 1.5rem;
    line-height: 2rem;
    border-bottom: 3px dashed #000000;
  }

  .generalInfo {
    font-size: 1rem;
    column-gap: 1rem;
    margin-bottom: 1rem;
  }

  .descriptionItem {
    width: auto;
    padding-right: 2rem;
  }

  .descriptionItem:last-child {
    padding-right: 0;
  }

  .avatar {
    top: 0;
    left: 0;
    width: 6rem;
  }
}

@media (max-width: 576px) {
  .title {
    font-size: 1.25rem;
    line-height: 1.5rem;
    border-bottom: 2px dashed #000000;
  }
}

@media (max-width: 500px) {
  .descriptionItem {
    width: auto;
  }

  .generalInfo {
    flex-direction: column;
    align-items: flex-end;
  }
}
